import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

// 路由懒加载
const index = (resolve) => {
  import('@/components/pages/index').then((module) => {
    resolve(module)
  })
}
export default new Router({
	//mode: 'history',  // 默认为hash，用hash模式的话，页面地址会变成被加个#号， http://localhost:8080/#/user
  routes: [{
			path: '/',
			redirect: '/index'
		},
    {
			path: '/index',
			component: resolve => require(['../components/common/shelf.vue'], resolve),
			children: [{
					path: '/',
					component: resolve => require(['../components/pages/index.vue'], resolve)
				},
				{
					path: '/service-window',
					component: resolve => require(['../components/pages/service-window.vue'], resolve)
				},
				{
					path: '/info-disclosure',
					component: resolve => require(['../components/pages/info-disclosure.vue'], resolve)
				},
				{
					path: '/policy-info',
					component: resolve => require(['../components/pages/policy-info.vue'], resolve)
				},
				{
					path: '/charging-service',
					component: resolve => require(['../components/pages/charging-service.vue'], resolve)
				},
				{
					path: '/data-publicity',
					component: resolve => require(['../components/pages/data-publicity.vue'], resolve)
				},
				{
					path: '/industry-standard',
					component: resolve => require(['../components/pages/industry-standard.vue'], resolve)
				},
				{
					path: '/about',
					component: resolve => require(['../components/pages/about.vue'], resolve)
				}
			]
		}
  ]
})



